import { useState, useRef } from 'react';

export default function Navbar() {
  const [menuOpen, setMenuOpen] = useState(false);
  const timerRef = useRef(null);

  const handleMouseEnter = () => {
    if (timerRef.current) {
      clearTimeout(timerRef.current);
      timerRef.current = null;
    }
    setMenuOpen(true);
  };

  const handleMouseLeave = () => {
    timerRef.current = setTimeout(() => {
      setMenuOpen(false);
    }, 3000);
  };

  return (
    <nav className="w-full bg-transparent py-2 px-8 flex items-center justify-between fixed top-0 left-0 z-50 font-serif" style={{ fontFamily: 'Merriweather, Times New Roman, serif' }}>
      <div className="flex items-center">
        <img src="/logo.png" alt="Banquet Hall Logo" className="w-16 h-16 mr-3" />
      </div>
      <div className="relative">
        <button
          className="flex flex-col justify-center items-center w-10 h-10 group"
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
          aria-label="Open navigation menu"
        >
          <span className="block w-8 h-1 bg-[#E5B200] mb-1 rounded transition-all duration-300"></span>
          <span className="block w-8 h-1 bg-[#E5B200] mb-1 rounded transition-all duration-300"></span>
          <span className="block w-8 h-1 bg-[#E5B200] rounded transition-all duration-300"></span>
        </button>
        {menuOpen && (
          <div
            className="absolute top-1/2 left-0 transform -translate-y-1/2 z-50 px-8 bg-[#2D1F14] bg-opacity-95 shadow-xl rounded-l-xl flex flex-row items-center space-x-8 text-lg font-semibold"
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            style={{ position: 'absolute', height: 'auto', background: '#2D1F14', opacity: 0.97 }}
          >
            <a href="#" className="text-[#E5B200] hover:text-[#F8EBC0] transition-colors">Home</a>
            <a href="#about" className="text-[#E5B200] hover:text-[#F8EBC0] transition-colors">About Us</a>
            <a href="#highlights" className="text-[#E5B200] hover:text-[#F8EBC0] transition-colors">Highlights</a>
            <a href="#services" className="text-[#E5B200] hover:text-[#F8EBC0] transition-colors">Services</a>
            <a href="#packages" className="text-[#E5B200] hover:text-[#F8EBC0] transition-colors">Packages</a>
            <a href="#testimonials" className="text-[#E5B200] hover:text-[#F8EBC0] transition-colors">Testimonials</a>
            <a href="#gallery" className="text-white hover:text-[#E5B200] transition-colors">Gallery</a>
            <a href="#contact" className="text-[#E5B200] hover:text-[#F8EBC0] transition-colors">Contact Us</a>
          </div>
        )}
      </div>
    </nav>
  );
}
